import Motion from '../../src/Motion'

export default {
  props: ['sizes', 'current'],

  computed: {
    left () {
      let cumulated = 0
      for (let i = 0; i < this.current; ++i) {
        cumulated += this.sizes[i].width
      }
      return -cumulated
    },
  },

  created () {
    console.log('hey')
  },

  components: { Motion },
}



