import Motion from '../../src/Motion'
import PhotosContainer from './PhotosContainer'

const base = 'https://github.com/posva/vue-motion/raw/master/docs/static/'

export default {
  data () {
    return {
      current: 0,
    }
  },

  computed: {
    sizes () {
      const current = this.photos[this.current]
      return this.photos.map(photo => ({
        width: photo.width * (current.height / photo.height),
        height: current.height,
      }))
    },
    sizesNormalized () {
      return this.sizes.reduce((res, size, i) => {
        res.pictures[i] = size
        return res
      }, {
        layout: {
          width: this.photos[this.current].width,
          height: this.photos[this.current].height,
        },
        pictures: [],
      })
    },
    photos () {
      return [
        {
          width: 300,
          height: 450,
          src: `${base}cat1.jpg`,
        },
        {
          width: 500,
          height: 390,
          src: `${base}cat2.jpg`,
        },
        {
          width: 500,
          height: 330,
          src: `${base}cat3.jpg`,
        },
        {
          width: 491,
          height: 251,
          src: `${base}cat4.jpg`,
        },
        {
          width: 447,
          height: 500,
          src: `${base}cat5.jpg`,
        },
        {
          width: 320,
          height: 154,
          src: 'https://media.giphy.com/media/JEVqknUonZJWU/giphy.gif',
        },
      ]
    },
  },

  methods: {
    next () {
      if (++this.current >= this.photos.length) {
        this.current = 0
      }
    },
    previous () {
      if (--this.current < 0) {
        this.current = this.photos.length - 1
      }
    },
    leftSpace (sizes) {
      const lefts = Array(this.photos.length)

      let cumulated = 0
      for (let i = this.current; i < this.photos.length; ++i) {
        lefts[i] = cumulated
        cumulated += sizes[`w${i}`]
      }

      if (this.current > 0) {
        cumulated = 0
        for (let i = this.current - 1; i >= 0; --i) {
          cumulated -= sizes[`w${i}`]
          lefts[i] = cumulated
        }
      }
      return lefts
    },
  },

  components: { Motion, PhotosContainer },
}



