import Motion from '../../src/Motion'
import VueSvg from './VueSvg'

export default {
  data () {
    return {
      x: -1000,
      rotated: 0,
    }
  },

  mounted () {
    setTimeout(() => {
      this.x = 0
    }, 1000)
  },

  methods: {
    end () {
      this.timer = setTimeout(() => {
        this.timer = null
        this.rotated = this.rotated === 0
                     ? 180
                     : 0
      }, 10000)
    },

    animate () {
      if (this.timer) clearTimeout(this.timer)
      this.rotated += 360
    },
    style (x) {
      return {
        overflow: Math.abs(x) > 10
                ? 'hidden'
                : 'initial',
      }
    },
  },

  computed: {
    spring () {
      return {
        stiffness: 180,
        damping: 12,
        precision: 0.01,
      }
    },
    values () {
      return {
        x: this.x,
        rotated: this.rotated,
      }
    },
  },

  components: { Motion, VueSvg },
}



